.contacts-form {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 30px;
  background-color: @basis-white;
  order: -1;
  border: none;
  padding-top: 30px;
  padding-bottom: 25px;

  @media (min-width: @tablet-width) {
    border: 2px solid @basis-light-grey;
    border-radius: 1px;
    margin-left: 20px;
    margin-right: 20px;
    margin-bottom: 59px;
  }

  @media (min-width: @desktop-width) {
    width: 100%;
  }
}

.contacts-form__legend {
  font-size: 14px;
  line-height: 30px;
  text-transform: uppercase;
  color: @basis-red;
  text-align: center;

  @media (max-width: @mobile-width-only) {
    display: none;
  }
}

.contacts-form__label {
  text-transform: uppercase;
  color: @basis-red;
  display: block;
  padding-left: 24px;

  @media (min-width: @tablet-width) {
    order: 2;
    margin: 0;
    padding: 0;
    align-self: flex-start;
    padding-left: 66px;
  }
}

.contacts-form__input {
  @media (min-width: @tablet-width) {
    width: 496px;
    margin-bottom: 10px;
    position: relative;
  }
}

.contacts-form__wrapper {
  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 32px;

    &:last-child {
      margin-bottom: 2px;
    }
  }
}
