.application {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 30px;
  color: @basis-blue;
  border: none;
  position: relative;
  padding-top: 115px;
  padding-bottom: 20px;

  @media (max-width: @mobile-width-only){
    background-color: @basis-extra-grey;
    }

  @media (min-width: @tablet-width) {
    border: 2px solid @basis-light-grey;
    border-radius: 1px;
    order: -2;
    margin-left: 20px;
    margin-right: 20px;
    padding-top: 50px;
    padding-bottom: 32px;
    margin-bottom: 53px;
  }

  @media (min-width: @desktop-width) {
    flex-grow: 1;
  }
}

.application__text {
  font-size: 14px;
  line-height: 18px;
  color: @basis-red;
  text-align: center;
  text-transform: uppercase;
  position: absolute;
  top: 40px;
  left: 0;

  @media (min-width: @tablet-width) {
    position: relative;
    top: auto;
  }
}

.application__list {
  list-style: none;
}

.application__item {
  margin-bottom: 45px;

  &:last-child {
    margin-bottom: 25px;
  }
}

.application__item label {
  padding-left: 43px;

  @media (min-width: @tablet-width) {
    padding-left: 80px;
  }
}
