.offer {
  background-color: @basis-extra-grey;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  text-align: center;
  color: @basis-blue;
  padding-bottom: 286px;
  position: relative;

  @media (min-width: @tablet-width) {
    padding-bottom: 0;
  }

}

.offer::after {
  content: "";
  position: absolute;
  background-image: url("../img/iphone-screen-mobile@1x.png");
  width: 157px;
  height: 280px;
  left: 35%;
  bottom: 0;

  @media (min-width: @tablet-width) {
    background-image: url("../img/iphone-screen-3-tablet@1x.png");
    width: 270px;
    height: 474px;
    position: absolute;
    background-repeat: no-repeat;
    left: 69px;
    top: 250px;
  }

  @media (min-width: @desktop-width) {
    display: none;
  }
}

.offer__header {
  position: relative;
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 120%;
  text-align: center;
  color: @basis-blue;
  background-color: @basis-white;
  padding-top: 42px;
  padding-bottom: 70px;

  @media (min-width: @tablet-width) {
    font-weight: 300;
    font-size: 40px;
    line-height: 54px;
    padding-top: 53px;
    padding-bottom: 13px;
  }

  @media (min-width: @desktop-width) {
    font-weight: 300;
    font-size: 50px;
    line-height: 68px;
    padding-top: 63px;
    padding-bottom: 84px;
  }
}

.offer__header::after{
  content: "";
  width: 1205px;
  height: 102px;
  background-image: url("data:image/svg+xml,%3Csvg width='1205' height='102' viewBox='0 0 1205 102' fill='%23f2f2f2' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 101.5L1204 1.5V101.5H1Z' /%3E%3C/svg%3E%0A");
  left: 0px;
  background-repeat: no-repeat;
  position: absolute;
  bottom: -2px;
  background-color: transparent;

  @media (min-width: @tablet-width) {
    width: 1000px;
    left: 0px;
    bottom: -80px;
    background-color: @basis-white;
  }
}

.offer__intro {
  margin: 0;
  margin-bottom: 31px;

  @media (min-width: @tablet-width) {
    margin: 0;
  }
}

.offer__slogan {
  margin: 0;

  @media (min-width: @tablet-width) {
    width: 340px;
    margin-left: 140px;
  }

  @media (min-width: @desktop-width) {
    width: 659px;
  }
}

.offer__slogan--footer {
  @media (min-width: @tablet-width) {
    width: 300px;
    margin-left: 0;
    margin-bottom: 11px;
  }

  @media (min-width: @desktop-width) {
    font-weight: normal;
    font-size: 18px;
    line-height: 30px;
    width: 522px;
    margin-bottom: 32px;
  }
}


.offer__list {
  list-style: none;
  padding-left: 0;
  padding-top: 44px;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: 310px;
    margin-left: 378px;
    padding-top: 91px;
  }

  @media (min-width: @desktop-width) {
    margin: 0 auto;
    padding: 0;
    padding-top: 329px;
    padding-bottom: 60px;
    display: flex;
    justify-content: flex-start;
  }
}

.offer__item {
  margin-bottom: 31px;
  position: relative;

  @media (min-width:@tablet-width) {
    margin-bottom: 35px;
  }

  @media (min-width: @desktop-width) {
    margin-left: 32px;
    margin-right: 53px;

    &:nth-child(1)::after {
      content: "";
      position: absolute;
      width: 268px;
      height: 302px;
      top: -357px;
      left: -26px;
      background-image: url(../img/iphone-screen-1-desktop@1x.png);
    }

    &:nth-child(2)::after {
      content: "";
      position: absolute;
      width: 268px;
      height: 302px;
      top: -357px;
      left: -26px;
      background-image: url(../img/iphone-screen-2-desktop@1x.png);
    }

    &:nth-child(3)::after {
      content: "";
      position: absolute;
      width: 268px;
      height: 302px;
      top: -357px;
      left: -26px;
      background-image: url(../img/iphone-screen-3-desktop@1x.png);
    }
  }
}

.offer__title {
  margin: 0;
  font-weight: bold;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  margin-bottom: 12px;

  @media (min-width:@tablet-width) {
    text-align: left;
  }

  @media (min-width: @desktop-width) {
    font-weight: bold;
    font-size: 30px;
    line-height: 30px;
    text-align: center;
    margin-bottom: 23px;
    width: 235px;
  }
}

.offer__description {
  margin: 0;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;

  @media (min-width:@tablet-width) {
    text-align: left;
  }

  @media (min-width: @desktop-width) {
    font-weight: normal;
    font-size: 18px;
    line-height: 30px;
    text-align: center;
  }
}

.offer__item:first-child .offer__description {
  @media (min-width: @desktop-width) {
  width: 238px;
  }
}

.offer__footer {
  background-color: @basis-white;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: @basis-black;
  padding-top: 96px;
  padding-left: 377px;
  padding-bottom: 30px;
  text-align: left;
  position: relative;

  @media (max-width: @mobile-width-only) {
    display: none;
  }

  @media (min-width: @desktop-width) {
    background-color: @basis-extra-grey;
    padding-bottom: 113px;
    padding-top: 62px;
    padding-left: 620px;
  }
}

.offer__footer::before {
  content: "";
  background-image: url("data:image/svg+xml,%3Csvg version='1.1' fill='%23dcdcdc' id='Layer_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' width='298px' height='345.097px' viewBox='0 0 298 345.097' enable-background='new 0 0 298 345.097' xml:space='preserve'%3E%3Cpath d='M264.568,102.055l5.355-5.355c7.717-7.717,9.124-18.938,3.126-24.935c-5.997-5.997-17.217-4.59-24.934,3.127l-5.386,5.385 C221.768,63.292,196.11,51.875,168,48.297v-9.583h7.658c10.914,0,19.843-8.711,19.843-19.357S186.573,0,175.659,0h-53.317 c-10.913,0-19.842,8.711-19.842,19.357s8.929,19.357,19.842,19.357H130v9.583C56.681,57.629,0,120.244,0,196.097 c0,82.291,66.709,149,149,149c82.29,0,149-66.709,149-149C298,160.434,285.465,127.703,264.568,102.055z M149,306.264 c-60.844,0-110.167-49.323-110.167-110.167S88.156,85.93,149,85.93c60.843,0,110.167,49.323,110.167,110.167 S209.843,306.264,149,306.264z'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  width: 298px;
  height: 276px;
  left: 68px;
  top: 54px;


  @media (min-width: @desktop-width) {
    content: "24";
    font-style: normal;
    font-weight: bold;
    font-size: 96px;
    line-height: 163px;
    color: @basis-red;
    display: flex;
    justify-content: center;
    align-items: center;
    top: -27px;
    left: 250px;
    width: 298px;
    height: 345px;
    font-size: 120px;
    padding-top: 18px;
  }
}

.offer__footer::after {
  @media (min-width: @tablet-width) {
    content: "24";
    width: 170px;
    height: 170px;
    border-radius: 50%;
    border: 16px solid #D22856;
    background-color: transparent;
    position: absolute;
    top: 111px;
    left: 86px;
    font-style: normal;
    font-weight: bold;
    font-size: 96px;
    line-height: 163px;
    color: @basis-red;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  @media (min-width: @desktop-width) {
    content: "";
    width: 217px;
    height: 217px;
    border-radius: 50%;
    border: 17px solid #D22856;
    background-color: transparent;
    position: absolute;
    top: 41px;
    left: 276px;
  }
}

.offer__footer a {
  color: @basis-red;
  text-decoration: underline;

  @media (min-width: @desktop-width) {
    font-size: 18px;
    line-height: 30px;
  }
}

.offer__intro--footer {
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  width: 168px;
  margin-bottom: 10px;

  @media (min-width: @desktop-width) {
    width: auto;
    margin-bottom: 21px;
  }
}
