.social__link {
  margin: none;
  width: 48px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: 3px solid @basis-extra-grey;
}

.social__list {
  display: flex;
  justify-content: center;
  margin-bottom: 27px;

  @media (min-width: @tablet-width) {
    margin-left: -6px;
  }

  @media (min-width: @desktop-width){
    margin-left: 17px;
  }
}

.social__link:hover,
.social__link:focus {
  border: 3px solid @basis-blue;
}

.social__link:active {
  border: 3px solid fade(@basis-blue, 10%);
}

.social__icon {
  fill: @basis-red;
}

.social__link:active .social__icon {
  fill: fade(@basis-red, 30%);
}

.social__item {
  margin-right: 6px;
}
