.user-data__label {
  font-weight: normal;
  font-size: 14px;
  line-height: 30px;
  color: @basis-red;
  display: block;
  text-transform: uppercase;
  padding-left: 23px;
  margin-bottom: 7px;

  @media (min-width: @tablet-width) {
    padding-left: 0px;
    margin-bottom: 0px;
    margin-right: 20px;
    padding-top: 17px;
  }
}

.user-data__input {
  width: 279px;
  padding-top: 23px;
  padding-bottom: 19px;
  padding-left: 19px;
  box-sizing: border-box;
  margin-bottom: 17px;
  border: 2px solid @basis-light-grey;
  border-radius: 1px;

  &::placeholder {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 30px;
    color: @basis-blue;
  }

  @media (min-width: @tablet-width) {
    flex-grow: 1;
  }
}

.user-data__wrapper {
  @media (min-width: @tablet-width) {
    display: flex;
    margin-bottom: 37px;
  }
}

.user-data {
  padding-bottom: 26px;

  @media (min-width: @tablet-width) {
    order: -3;
    padding-bottom: 5px;
  }

  @media (min-width: @desktop-width) {
    flex-grow: 1;
  }
}
