.contacts {
  background-color: @basis-white;
  color: @basis-red;
  font-weight: 300;
  font-size: 24px;
  line-height: 33px;
  text-align: center;
  padding-top: 18px;

  @media (min-width: @tablet-width) {
    padding-top: 43px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 59px;
  }
}
.contacts a {
  display: block;
}

.contacts .container {
  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 22px;
  }
}

.contacts__wrapper {
  margin:0;
  margin-bottom: 16px;

  @media (min-width: @tablet-width) {
    width: 300px;
    margin-right: 10px;
    margin-bottom: 42px;
  }
}

.contacts__wrapper--special {
  @media (min-width: @desktop-width) {
    order: 2;
    margin-right: 5px;
  }
}

.contacts__text {
  font-weight: 300;
  font-size: 24px;
  line-height: 120%;
  color: @basis-blue;
  margin-bottom: 34px;

  @media (min-width: @tablet-width) {
    font-size: 40px;
    line-height: 54px;
    text-align: center;
    width: 370px;
    margin: 0 auto;
    margin-bottom: 32px;
  }

  @media (min-width: @desktop-width) {
    font-size: 50px;
    line-height: 68px;
    width: 634px;
    margin-bottom: 39px;
  }
}

.contacts__title {
  display: block;
  font-weight: bold;
  font-size: 18px;
  line-height: 25px;
  color: @basis-blue;

  @media (min-width: @tablet-width) {
    margin-bottom: 3px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 19px;
  }
}

.contacts__tel,
.contacts__email {
  color: @basis-red;
  font-weight: 300;
  font-size: 24px;
  line-height: 33px;
  text-align: center;
  text-decoration-line: none;
}

.contacts__adress {
  @media (min-width: @tablet-width) {
    margin: 0 auto;
    width: 298px;
  }
}

.contacts__map img{
  max-width: 320px;
  height: 208px;

  @media (min-width: @tablet-width) {
    height: 374px;
    max-width: 660px;
  }

  @media (min-width: @desktop-width) {
    height: 493px;
    max-width: 1200px;
  }
}

.contacts__map {
  position: relative;
}

.contacts__map::before {
  content: "";
  background-image: url(../img/bg-triangle-white-footer.svg);
  width: 1200px;
  height: 123px;
  top: 0px;
  position: absolute;
  background-repeat: no-repeat;
  background-position: center;
  fill: @basis-white;

  @media (min-width:@tablet-width) {
    top: -1px;
    left: 167px;
  }

  @media (min-width: @desktop-width) {
    width: 1439px;
    height: 123px;
    top: -30px;
    left: -3px;
  }
}
