.copyright__icon {
  fill: @basis-red;
}

.copyright__icon:hover,
.copyright__icon:focus {
  fill: @basis-blue;
}

.copyright__icon:active {
  fill: fade(@basis-blue, 30%);
}

.copyright__text {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  position: relative;

  @media (min-width: @desktop-width) {
    font-size: 18px;
  }
}

.copyright__link {
  position: absolute;
  top: 203px;
  left: 176px;

  @media (min-width: @tablet-width) {
    right: 0;
    left: auto;
    top: 41px;
  }

  @media (min-width: @desktop-width) {
    top: 71px;
  }
}

.copyright {
  padding-top: 20px;
  padding-left: 72px;
  text-align: left;

  @media (min-width: @tablet-width) {
    padding-top: 18px;
    padding-left: 0;
    padding-right: 39px;
  }

  @media (min-width: @desktop-width) {
    padding-right: 33px;
  }
}
