.price {
  background-color: @basis-extra-grey;
  padding-top: 81px;
  padding-bottom: 27px;

  @media (min-width: @tablet-width) {
    padding-top: 48px;
    padding-bottom: 9px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 48px;
    padding-bottom: 98px;
  }
}

.price__list {
  margin-bottom: 33px;
}

.price__list::before {
  content: "";
  height: 179px;
  width: 20px;
  background-color: @basis-white;
  position: absolute;
  left: 131px;

  @media (min-width: @tablet-width) {
    display: none;
  }
}

.price__list--top {
    @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

.price__list .slider {
  @media (max-width: @mobile-width-only) {
    &:first-child {
      display: none;
    }

    &:last-child {
      display: none;
    }
  }
}

.price__header h4,
.price__header p {
  margin: 0;
}

.price__header {
  background-color: @basis-blue;
  font-size: 16px;
  line-height: 30px;
  text-align: center;
  text-transform: uppercase;
  color: @basis-white;
  padding-top: 12px;
  box-sizing: border-box;
  position: relative;
}

.price__header::before{
  background-color: @basis-red;
  width: 102px;
  height: 23px;
  position: absolute;
  top: 16px;
  left: -22px;
  content: "Хит";
  transform: rotate(-45deg);
  overflow: hidden;
  font-size: 14px;
  line-height: 30px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.price__item {
  background-color: @basis-white;
}

.price__item p {
  margin: 0;
  text-transform: uppercase;
  display: flex;
  justify-content: flex-start;
  padding-bottom: 16px;
  padding-top: 11px;
  padding-left: 20px;
  border-bottom: 3px solid @basis-extra-grey;
  font-size: 14px;
  line-height: 30px;
}

.price__header p {
  font-weight: 300;
  font-size: 18px;
  line-height: 30px;
  padding-bottom: 25px;
  padding-top: 0px;
  justify-content: center;
}

.price__toggles {
  margin: 0;
  padding-left: 109px;

  @media (min-width: @tablet-width) {
    display: none;
  }
}
