.page-footer {
  background-color: @basis-white;
  color: @basis-blue;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  position: relative;
}

.page-footer__wrapper {
  padding-top: 35px;

  @media (min-width: @tablet-width) {
    display: flex;
    justify-content: space-between;
    padding-top: 30px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 61px;
  }
}

.page-footer__logo {
  margin-bottom: 32px;
}
