.reviews {
  background-color: @basis-white;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 30px;
  color: @basis-blue;
  text-align: center;
  padding-top: 41px;
  padding-bottom: 67px;
  position: relative;

  @media (min-width: @tablet-width) {
    font-size: 18px;
    padding-top: 75px;
    padding-bottom: 60px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 165px;
    padding-bottom: 122px;
  }
}

.reviews__list {
  margin-bottom: 17px;

  @media (min-width: @tablet-width) {
    margin-bottom: 21px;
  }
}

.reviews__item {
  margin: 0;
}

.reviews__author-name {
  font-style: normal;
  font-weight: bold;
  font-size: 18px;
  line-height: 30px;
  color: @basis-black;

  @media (min-width: @tablet-width) {
  font-weight: bold;
  font-size: 20px;
  margin-top: 17px;
  }

  @media (min-width: @desktop-width) {
    font-size: 24px;
  }
}

.reviews__toggles {
  margin: 0;
  padding: 0;

  @media (min-width: @desktop-width) {
    display: none;
  }
}

.reviews__author-info {
  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: column;
  }
}

.reviews__text {
  display: block;

  @media (min-width: @tablet-width) {
    max-width: 620px;
    order: -1;
  }

  @media (min-width: @desktop-width) {
    max-width: 785px;
    padding-left: 78px;
    margin-bottom: 21px;
  }
}

.reviews::after {
  content: "";
  width: 1205px;
  height: 102px;
  background-image: url("data:image/svg+xml,%3Csvg width='1205' height='102' viewBox='0 0 1205 102' fill='%23f2f2f2' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 101.5L1204 1.5V101.5H1Z' /%3E%3C/svg%3E%0A");
  left: 0px;
  background-repeat: no-repeat;
  position: absolute;
  bottom: -1px;
  background-color: transparent;

  @media (min-width: @tablet-width) {
    width: 1200px;
    left: 0;
    background-color: @basis-white;
  }
}

.reviews::before {
  @media (max-width: @mobile-width-only) {
    display: none;
  }

  @media (min-width: @tablet-width) {
    display: none;
  }

  @media (min-width: @desktop-width) {
    content: "";
    display: block;
    width: 79px;
    height: 63px;
    background-image: url(../img/icon-quotes.svg);
    position: absolute;
    top: 69px;
    left: 682px;
      fill: @basis-red;
  }
}

.reviews__experience {
  @media (max-width: @mobile-width-only) {
    display: none;
  }

  @media (min-width: @tablet-width) {
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 30px;
  }

  @media (min-width: @desktop-width) {
    font-size: 18px;
  }
}
