.slider__toggle {
  margin: 0;
  padding: 0;
  margin-right: 13px;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: fade(@basis-blue, 30%);
  border: none;
}

.slider__toggle--current {
  background-color: @basis-white;
  border: 4px solid @basis-blue;
  width: 12px;
  height: 12px;
}

.slider__toggles--arrows {
  @media (max-width: @mobile-width-only) {
    display: none;
  }

  @media (min-width: @tablet-width) {
    display: none;
  }

  @media (min-width: @desktop-width) {
    display: inline;
  }
}

.slider__toggle-arrow {
  width: 22px;
  height: 41px;
  position: relative;
  background-color: inherit;
  border: none;
}

.slider__toggle-arrow--left {
  position: absolute;
  top: 210px;
  left: 244px;
}

.slider__toggle-arrow--right {
  position: absolute;
  top: 210px;
  left: 1162px;
}
