.record {
  position: relative;
  @media (max-width: @mobile-width-only){
  background-color: @basis-extra-grey;
  border: none;
  padding-top: 116px;
  }

  @media (min-width: @tablet-width) {
    border: 2px solid @basis-light-grey;
    border-radius: 1px;
    margin-left: 20px;
    margin-right: 20px;
    padding-top:46px;
    margin-bottom: 58px;
  }

  @media (min-width: @desktop-width) {
    width: 100%
  }
}

.record__legend {
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  text-transform: uppercase;
  color: @basis-red;
  position: absolute;
  top: 39px;
  left: 30px;
  width: 252px;

  @media (min-width: @tablet-width) {
    position: relative;
    top: auto;
    left: auto;
    width: auto;
  }

}

.record__list {
  list-style: none;
}

.record__item {
  margin-bottom: 53px;

  &:last-child {
    margin-bottom: 33px;
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 52px;

    &:last-child {
      margin-bottom: 42px;
    }
  }
}

.record__title {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 30px;
  color: @basis-blue;
  margin-left: 39px;

  @media (min-width: @tablet-width) {
    margin-left: 78px;
  }
}
