.downloads {
  color: @basis-white;
  font-weight: 300;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
  background-color: @basis-blue;
  padding-top: 28px;
  padding-bottom: 17px;

  @media (min-width: @tablet-width) {
    position: absolute;
    background-color: transparent;
    top: 604px;
    left: 68px;
    height: 200px;
  }

  @media (min-width: @desktop-width) {
    top: 420px;
    left: 250px;
  }
}

.downloads__link {
  display: block;
  font-weight: bold;
  font-size: 18px;
  line-height: 18px;
  color: @basis-white;
  text-align: center;
  width: 280px;
  margin: 0 auto;
  padding-top: 15px;
  padding-bottom: 16px;
  box-sizing: border-box;
  border: 3px solid @basis-white;
  border-radius: 29px;
  margin-bottom: 27px;

  &:hover,
  &:focus {
    background-color: @basis-white;
    color: @basis-red;
  }

  &:active {
    color: fade(@basis-red, 30%);
  }

  @media (min-width: @tablet-width) {
    margin: 0;
    margin-bottom: 43px;
    width: 300px;
  }

  @media (min-width: @desktop-width) {
    font-weight: bold;
    font-size: 21px;
    line-height: 18px;
    margin-bottom: 65px;
  }
}

.downloads__list {
  display: flex;
  justify-content: flex-start;
  align-items: baseline;
  margin-bottom: 17px;

  @media (min-width: @tablet-width) {
    margin-bottom: 26px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 44px;
  }
}


.downloads__item {
  margin-left: 36px;

  @media (min-width: @tablet-width) {
    margin-left: 41px;
  }
}

.downloads__text {
  @media (min-width: @tablet-width) {
    font-weight: 300;
    font-size: 18px;
    margin-left: -31px;
  }
}

.downloads .container {

  @media (min-width: @tablet-width) {
  width: 330px;
  }
}
