.tender {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 30px;
  background-color: @basis-white;
  color: @basis-blue;
}

.tender__form {
padding-top: 20px;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: column;
  }

  @media (min-width: @desktop-width) {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.tender__title {
  margin: 0;
  font-weight: 300;
  font-size: 24px;
  line-height: 33px;
  text-align: center;
  color: @basis-white;
  display: flex;
  background-image: url(../img/bg-back-mobile@1x.jpg);
  background-color: @basis-blue;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 27px;
  padding-bottom: 44px;

  @media (min-width: @tablet-width) {
    font-size: 50px;
    line-height: 68px;
    padding-top: 67px;
    padding-bottom: 59px;
  }
}

.tender__text {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 30px;
  text-align: center;
  margin: 0;
  margin-top: 44px;

  @media (min-width: @tablet-width) {
    font-size: 18px;
    width: 490px;
    margin-top: 91px;
    padding-left: 65px;
  }
}

.tender__text--form {
  margin-top: 0px;
  @media (max-width: @mobile-width-only) {
    display: none;
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 53px;
  }
}

.tender__button {
  background: @basis-dark-green;
  border: 2px solid @basis-dark-green;
  border-radius: 1px;
  font-weight: bold;
  font-size: 18px;
  line-height: 30px;
  text-transform: uppercase;
  color: @basis-white;
  width: 100%;
  padding-top: 14px;
  padding-bottom: 14px;

  @media (min-width: @tablet-width) {
    width: 296px;
  }
}

.tender__text--bottom {
  text-transform: uppercase;
  margin-top: 35px;
  margin-bottom: 21px;
}

.tender__text--special {
  color: @basis-red;
}
