.feedback {
  border: none;
  margin-bottom: 29px;

  @media (min-width: @tablet-width) {
    border: 2px solid @basis-light-grey;
    border-radius: 1px;
    margin-left: 20px;
    margin-right: 20px;
    padding-top: 22px;
    padding-bottom: 46px;
    margin-bottom: 68px;
  }

  @media (min-width: @desktop-width) {
    width: 100%
  }
}

.feedback .container {
  @media (min-width: @tablet-width) {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.feedback__title {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  text-transform: uppercase;
  color: @basis-red;
  padding-top: 38px;
  margin-bottom: 10px;

  @media (min-width: @tablet-width){
    padding-top: 0;
  }
}

.feedback__form {
  font-size: 18px;
  line-height: 30px;
  color: @basis-blue;
  border: 2px solid @basis-light-grey;
  border-radius: 1px;
  padding-top: 15px;
  padding-left: 19px;
  padding-right: 10px;
  width: 280px;
  box-sizing: border-box;

  &::placeholder {
    font-family: "Open Sans", Arial, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 30px;
  }

  @media (min-width: @tablet-width) {
    width: 496px;
    margin: 0 auto;
  }

}
